* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

input, button {
    appearance: none;
    background: none;
    border: none;
    outline: none;
}

.App {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #53565A;
}

form {
    display: block;
    position: relative;   
}

form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: 1;
    background-image: linear-gradient(to bottom right, #aba2d4, #0011ff);
}


form .from-inner {
    position: relative;
    display: block;
    background-color: white;
    padding: 30px;
    z-index: 2;
}

form .from-inner h2 {
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

form .from-inner .from-group {
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

form .from-inner .from-group label {
    display: block;
    color: #666;
    font-size: 12px;
    margin-bottom: 5px;
    transition: 0.4s;
}

form .from-inner .from-group:focus-within label {
    color: #6a67ff; 
}

form .from-inner .from-group input {
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #F8F8F8;
    border-radius: 8px;
    transition: 0.4s;
}

form .from-inner .from-group input:focus {
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}


form .from-inner input[type="submit"] {
    display: inline-block;
    padding: 10px 15px;
    border-radius: 8px;
    background-image: linear-gradient(to right, #aba2d4 50%, #aba2d4 50%, #0011ff);
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color: #FFF; 
    font-weight: 700;
    cursor: pointer;
}

form .from-inner input[type="submit"]:hover {
    background-position: 100% 0%;
}